*, ::before, ::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  color: #ffffff;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  font-family: "Inter", sans-serif;
  background-color: #111;
}
body.lock {
  overflow: hidden;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
