.nav {
  z-index: 10;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(17, 17, 17, 0.9);
}
.nav__burger {
  display: none;
  width: 60px;
  height: 36px;
  position: relative;
  z-index: 5;
  cursor: pointer;
}
@media (max-width: 80em) {
  .nav__burger {
    display: block;
  }
}
@media (max-width: 48em) {
  .nav__burger {
    width: 30px;
    height: 18px;
  }
}
.nav__burger span {
  transition: all 0.2s;
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
}
@media (max-width: 48em) {
  .nav__burger span {
    height: 2px;
  }
}
.nav__burger span:first-child {
  top: 0;
}
.nav__burger span:last-child {
  top: auto;
  bottom: 0;
}
.nav__burger.active span {
  transform: scale(0);
}
.nav__burger.active span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
}
.nav__burger.active span:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
}
.nav__container {
  height: 102px;
  display: flex;
  justify-content: space-between;
